@media (max-width:1200px) {
    .design__content, .techno__content {
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px;
    }
    .design__title, .techno__title {
        text-align: center;
    }
    .design__text, .techno__text {
        text-align: center; 
        margin-top: 10px;
        margin: 0;
        padding: 10px;
        margin: 10px;
    }
    .techno__text {
        order: 2;
        max-width: 500px;
    }
    .design__img {
        img {
            width: 100%;
        }
    }
    .techno__img {
        order: 1;
        img {
        width: 100%;
    }
}
}

@media(max-width: 778px) {
    .header__text {
        display: none;
    }
    .header__img {
        display: none;
    }
    .menu-btn {
        display:block;
    }
    .header__button {
        display: none;
    }
}
@media (max-width: 568px) {
    .footer__content {
        font-size: 12px;
    }
    
}
@media (max-width: 468px) {
    .header__logo{
        width: 150px;
        img {
            width: 100%;
        }
    }
    .footer__wrapper {
        flex-wrap: wrap;
        padding: 10px;
        justify-self: center;
        align-items: center;
    }
    
}