.footer {
    background-color: #142F4F;
    padding: 40px 40px;
    &__title {
        margin-bottom: 15px;
    }
    &__wrapper {
        display: flex;
        justify-content: space-between;
        color: #ffff;
    }
    &__text {
        p{
        max-width: 200px;
        }
    }
    &__content {
        ul{
            list-style: none;
            li {
                margin-bottom: 15px;
            a {
                text-decoration: none;
                color: white;
            }
        }
    } 
}
    
}