.header {
    padding: 20px;
    &__wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &__content {
        display: flex;
        align-items: center;
    }
    &__text {
        margin-right: 20px;

        a{
        text-decoration: none;
        color: black;
        font-weight: bold;
        font-size: 22px;
    }
}
    &__subtitle {
       color: #797f7e;
       margin-bottom: 5px;
    }
    &__button {
        background-color: #fff;
        border: 1px solid #4a9dde;
        width: 175px;
        padding: 10px;
        border-radius: 5px;
        color:#4a9dde;
        font-weight: 700;
        cursor: pointer;
        outline: none;
        box-shadow: 0 0 38px 0 rgba(0,0,0,.8);
        transition: .5s all ease;
        box-shadow: 5px 5px 0px rgba(#4a9dde, 0.5);
        &:hover {
            background-color: #4a9dde;
            color: #fff;
            transform: translateY(5px);
            border: 2px solid #4a9dde;
            box-shadow: 0 0 38px 0 rgba(#4a9dde, 0.5);
            box-shadow: 3px 3px 0px rgba(#4a9dde, 0.5);
        }
    }
} 

.background {
    height: 399px ;
    background: url(../img/jpg/bg.jpg) center (center/cover) no-repeat;
}     