.overlay {
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    background-color: rgba(0,0,0,0.56);
    display: none;
}
.modal {
    display: none;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    background-color: #f2f2f2;
    min-height: 400px;
    padding: 30px 30px 30px 30px;
    display: block;
    &__title {
        text-align: center;
    }
    &__subtitle {
        text-align: center;
    }
    &__close {
        cursor: pointer;
        position: absolute;
        top: -30px;
        right: -40px;
        font-size: 50px;
        color: #142f4f;
    }
}

.feed-form {
    margin: 36px;
    display: flex;
    flex-direction: column;
    width: 300px;
    justify-content: center;
    align-items: center;
    input {
        width: 300px;
        height: 50px;
        border-radius: 5px;
        background-color: #ffffff;
        margin-bottom: 15px;
        border: none;
        padding: 0 20px; 
        color: #4b4b4b;
        font-size: 14px;
        font-weight: 300;
        text-align: center;
    }
    .btn {
        background-color: #142f4f;
        color: #ffffff;
        }
}
