.popular {
    padding: 41px 0px;
    &__content {
        display: flex;
        flex-wrap: wrap;
    }
    &__item {
        max-width: 999px;
        margin-bottom: 10px;
        flex: 0 1 25%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }
    &__footer {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 50px;
        width: 290px;
        background-color: #142F4F; 
        font-size: 18px;
        color: #FFFFFF;
        transform: translateY(-4px);

    }
    &__title {
        margin: 41px 25px;
    }
    &__img {
        img {
            width: 290px;
            height: 220px;
        }
    }
}

@media (max-width: 1180px) {
    .popular__item {
        flex: 0 1 50%;
    }
}
@media (max-width:679px) {
    .popular__item {
        flex: 0 1 100%;
    }
}